import { Component, OnInit } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Global } from '../../../../global/global';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
@Component({
  selector: 'app-assign-lead2-staff',
  templateUrl: './assign-lead2-staff.component.html',
  styleUrls: ['./assign-lead2-staff.component.scss']
})
export class AssignLead2StaffComponent implements OnInit {
  options: string[] = [];
  assignStaff: any;
  constructor(
    public http: Http,
    public toastr: ToastrService,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public beyond: Beyond,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.listOfLeads()
  }

  listOfLeads() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getEmployees', options)
      .subscribe(data => {
        var staffData = data.json()
        if (staffData.status === 200) {
          for (var i = 0; i < staffData.response.length; i++) {
            this.options.push(staffData.response[i].email)
          }
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  assign() {
    let assignObj = {
      "email": this.assignStaff,
      "leadCode": this.activatedRoute.snapshot.params['id'],
    }
    this.http.post(Global.url + '/assignLeadToStaff', assignObj)
      .subscribe(data => {
        var assignData = data.json();
        if (assignData.status == 200) {
          this.toastr.success('Assigned Staff Successfully');
          this.router.navigate(['/leads/listLeads'])
          return false
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      });
  }

}
