import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../utilities/beyond';
import { multiplewizard } from '../../../../utilities/multiplewizard';
import { Imageselector } from 'src/utilities/imageselector';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global';
import { CustomValidators } from '../../../../utilities/customValidators';


@Component({
  providers: [Beyond, multiplewizard, Imageselector, CustomValidators],
  selector: 'app-view-lead',
  templateUrl: './view-lead.component.html',
  styleUrls: ['./view-lead.component.scss']
})
export class ViewLeadComponent implements OnInit {
  customerId: any;
  leaddCustomerData: any;
  customerData: any;
  serviceName: any;
  customerStatus: boolean = false;
  customerHistoryStatus: boolean = false;
  matchingStatus: boolean = false;
  getQuote: boolean = false;
  getProz: boolean = false;
  customerHistoryData: any;
  emergency: any;
  catSubCatName: any;
  categoryName: any;
  subCategoryName: any;
  editField: any;
  matchingProData: any;
  zipCode: any;
  leadStatus: any;
  leadUpdate: any;
  theEmergencyRequest: boolean = false;
  image: any;
  customerData2: string;
  loginData: any;
  vanSubDetails: any;
  customerID: any;
  newWorkOrder: boolean;
  displaynewWorkOrder: string;
  newworkOrderconfirmation = 'none';
  matchingResponse: any;
  matchingResponseStatus: boolean;
  isAssignStaff: any;
  isProMatched: any;
  isPurchased: any;
  status: any;
  leadStatusData: any;
  emergencyData: any;
  inBusinessSince: any;
  aggregateRating: any;
  companyName: any;
  emailId: any;
  phone: any;
  state: any;
  city: any;
  street: any;
  customer: any;
  lastName: any;
  firstName: any;
  customerImage: any;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.multiplewizard.Multiplewizard();
    this.imageselector.imageselector();
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.listLeadCustomerHistory(this.activatedRoute.snapshot.params['anyCode'])
    this.listLeadCustomer(this.activatedRoute.snapshot.params['anyCode'])
  }

  listLeadCustomerHistory(customerId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadHistoryDetailsViewLead/' + customerId + '/' + this.activatedRoute.snapshot.params['leadId'], options)
      .subscribe(data => {
        var leadCustomerHistoryData = data.json();
        if (leadCustomerHistoryData.status == 200) {
          this.customerHistoryStatus = true;
          this.customerHistoryData = leadCustomerHistoryData.response;
          for (var i = 0; i < this.customerHistoryData.length; i++) {
            if (this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "Yes") {
              this.customerHistoryData[i].color = 'red';
              this.customerHistoryData[i].colorRed = true;
              this.theEmergencyRequest = true;

            } else if (this.customerHistoryData[i].serviceQuestion == "Is this an emergency" && this.customerHistoryData[i].serviceAnswer == "No") {
              this.customerHistoryData[i].color = 'green';
              this.customerHistoryData[i].colorRed = false;
              this.theEmergencyRequest = false;
            } else if (this.customerHistoryData[i].serviceQuestion == "" && this.customerHistoryData[i].serviceAnswer == "") {
              this.customerHistoryData[i].color = 'green';
              this.customerHistoryData[i].colorRed = false;
              this.theEmergencyRequest = false;
            }
          }
        } else {
          this.customerHistoryStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  listLeadCustomer(customerId) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getLeadDetails/' + customerId, options)
      .subscribe(data => {
        var leadCustomerData = data.json();
        if (leadCustomerData.status == 200) {
          this.customerStatus = true;
          this.customerData = leadCustomerData.response[0];
          this.firstName = this.customerData.firstName
          this.lastName = this.customerData.lastName
          this.customer = this.customerData.customerStatus
          this.zipCode = this.customerData.zipCode
          this.street = this.customerData.street
          this.city = this.customerData.city
          this.state = this.customerData.state
          this.phone = this.customerData.phone
          this.emailId = this.customerData.emailId
          this.companyName = this.customerData.companyName
          this.serviceName = this.customerData.serviceName
          this.aggregateRating = this.customerData.aggregateRating
          this.inBusinessSince = this.customerData.inBusinessSince
          this.subCategoryName = this.customerData.subCategoryName
          this.emergencyData = this.customerData.emergency
          this.leadStatusData = this.customerData.leadStatus
          this.status = this.customerData.status
          this.isPurchased = this.customerData.isPurchased
          this.customerImage = this.customerData.customerImage
          this.isProMatched = this.customerData.isProMatched
          this.isAssignStaff = this.customerData.isAssignStaff
          this.image = this.customerData.image
          if (this.customerData.image == '' || this.customerData.image == null) {
            this.customerData2 = "../../assets/img/no-image.png"
          } else {
            this.customerData2 = Global.url + this.customerData.image
          }
          this.leadStatus = this.customerData.leadStatus
          this.zipCode = this.customerData.zipCode
          console.log("in lead cust" + JSON.stringify(this.customerData.zipCode))
        } else {
          this.customerStatus = false;
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  back() {
    window.history.back()
  }

  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list() {
    this.router.navigate(['/leads/listLeads'])
  }
}