import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { Beyond } from "../../../../utilities/beyond";
import { multiplewizard } from "../../../../utilities/multiplewizard";
import { Imageselector } from "src/utilities/imageselector";
import * as moment from 'moment';
import { Http, Headers, RequestOptions } from "@angular/http";
import { ToastrService } from "ngx-toastr";
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
} from "@angular/forms";
import { Global } from "../../../../global/global";
import { CustomValidators } from "../../../../utilities/customValidators";
declare var $: any;
const getOptions = () => {
  const headers = new Headers();
  headers.append("Accept", "application/json");
  headers.append("Content-Type", "application/json");
  return new RequestOptions({ headers: headers });
};

const parseResponse = (data) => {
  const json = data.json();
  if (json.status == 200 && Array.isArray(json.response)) {
    return json.response[0]["count(*)"];
  }
  return 0;
};
@Component({
  providers: [Beyond, multiplewizard, Imageselector, CustomValidators],
  selector: 'app-lead-purchase-history',
  templateUrl: './lead-purchase-history.component.html',
  styleUrls: ['./lead-purchase-history.component.scss']
})
export class LeadPurchaseHistoryComponent implements OnInit {
  leadPurchaseHistory: Array<any> = [];
  showData: boolean = false;
  loginData: any;
  constructor(
    public beyond: Beyond,
    public multiplewizard: multiplewizard,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public imageselector: Imageselector,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    this.loginData = JSON.parse(localStorage.getItem("login"));
    this.beyond.beyondAdmin();;
    this.listOfLeads();
  }

  listOfLeads() {
    const url = Global.url + "/leadPurchaseHistory/" + JSON.parse(localStorage.getItem("login")).loginCode;
    const successCallback = (data) => {
      const json = data.json();
      if (json.status == 200 && Array.isArray(json.response)) {
        this.leadPurchaseHistory = json.response;
        this.showData = true;
      }
    }
    this.http
      .get(url, getOptions())
      .subscribe(successCallback, this.errorCallback);
  }

  errorCallback() {
    this.toastr.warning("Unable to Connect to Server");
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
