import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AssignLead2StaffComponent } from './proz-leads/assign-lead2-staff/assign-lead2-staff.component';

import { AddLeadComponent } from './proz-leads/add-lead/add-lead.component';
import { ViewLeadComponent } from './proz-leads/view-lead/view-lead.component';
import { LeadsLayoutComponent } from './leads-layout/leads-layout.component';
import { ListLeadsComponent } from './proz-leads/list-leads/list-leads.component';
import { LeadPurchaseHistoryComponent } from './proz-leads/lead-purchase-history/lead-purchase-history.component';
const routes: Routes = [
  {
    path: '', component: LeadsLayoutComponent,
    children: [
      {
        path: 'assignleadToStaff/:id',
        component: AssignLead2StaffComponent
      },
      {
        path: 'listLeads',
        component: ListLeadsComponent
      },
      {
        path: 'addLead',
        component: AddLeadComponent
      },

      {
        path: 'viewLead/:anyCode/:leadId',
        component: ViewLeadComponent
      },
      {
        path: 'leadpurchasehistory',
        component: LeadPurchaseHistoryComponent
      },      
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LeadsRoutingModule { }
